import { useState, useEffect } from 'react';
import photo from './img/foto.png';

const Header = () => {
    const [menu, setMenu] = useState(false);
    const [navbar, setNavbar] = useState(false);

    useEffect(() => {
        changeBackground();
        window.addEventListener("scroll", changeBackground);
    })

    const toggleMenu = () => {
        setMenu((menu) ? false : true);
        console.log(menu);
    }

    const changeBackground = () => {
        if(window.scrollY >= 75) {
            setNavbar(true);
        } else {
            setNavbar(false);
        }
    }

    const show = (menu) ? " show" : "";
    const background = (menu || navbar) ? " bg-white" : "";

    return(
        <nav className={"navbar navbar-expand-lg navbar-light bg-light fixed-top" + background}>
            <div className="container">
                <a className="navbar-brand" href="/">
                    <img className="me-2" src={photo} alt="Foto Iqbal"></img> Mohammad Iqbal
                </a>
                <button className="navbar-toggler" type="button" onClick={ toggleMenu }>
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className={"collapse navbar-collapse" + show}>
                    <div className="navbar-nav text-center ms-auto py-4 py-lg-0">
                        <a className="nav-link mx-2" href="#home">Home</a>
                        <a className="nav-link mx-2" href="#resume">Resume</a>
                        <a className="nav-link mx-2" href="#skill">Skill</a>
                        <a className="nav-link mx-2" href="#portofolio">Portofolio</a>
                        <a className="nav-link mx-2" href="#certificate">Certificate</a>
                        <a className="nav-link mx-2" href="#contact">Contact</a>
                    </div>
                </div>
            </div>
        </nav>
    )
}

export default Header;